  border-radius: 4px;
  transition: all 0.3s;
}

.form__input:focus,
.form__area:focus {
  background: #fff;
  border: 1px solid rgba(255, 90, 48, 0.4);
  box-shadow: 0px 80px 80px -20px rgba(255, 90, 48, 0.08), 0px 30px 24px -10px rgba(255, 90, 48, 0.05), 0px 12px 10px -6px rgba(255, 90, 48, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  transition: all 0.3s;
}

.form__input::placeholder,
.form__area::placeholder {
  font-weight: 400;
  font-size: 14px;
  color: #9A9CA5;
}

.form__input:hover,
.form__area:hover {
  background-color: #fff;
  transition: all 0.2s;
}

.form__area {
  resize: none;
}

.value {
  transform: rotateX(90deg);
}

.value--transform {
  transform: rotateX(0);
  transition: all 0.9s;
}

.value-1 {
  transition-delay: 0.2s;
}

.value-2 {
  transition-delay: 0.4s;
}

.value-3 {
  transition-delay: 0.6s;
}

.service {
  transform: translateY(-200px);
