  transition: all 0.9s;
}

.service--transform {
  transform: translateY(0);
  opacity: 1;
  transition: all 0.9s;
}

.service-1 {
  transition-delay: 0.2s;
}

.service-2 {
  transition-delay: 0.4s;
}

.service-3 {
  transition-delay: 0.6s;
}

.service-4 {
  transition-delay: 0.8s;
