.burger {
  display: none;
  position: relative;
  width: 34px;
  height: 26px;
  z-index: 10;
  margin-right: 20px;
}

.burger__span {
  width: 100%;
  height: 3px;
  border-radius: 1.5px;
  position: absolute;
  left: 0;
  background-color: #ff5a30;
  transition: all 0.3s;
}

.burger__span--one {
  top: 0;
}

.burger__span--two {
  top: calc(50% - 1.5px);
}

.burger__span--three {
  top: calc(100% - 3px);
}

.burger__span-one-active {
  transform: rotate(45deg);
  top: calc(50% - 1.5px);
  transition: all 0.3s;
}

.burger__span-two-active {
  display: none;
  transition: all 0.3s;
}

.burger__span-three-active {
  transform: rotate(-45deg);
  top: calc(50% - 1.5px);
  transition: all 0.3s;
}

